<template>
  <div>
    <ul class="clearfix">
      <li
        v-for="(item , index) in listes[0]"
        :key="index"
        class="fl"
        @click="find(item)"
      >{{item.name}}</li>
    </ul>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      lists: []
    };
  },
  created() {
    this.lists.push(this.listes);
  },
  computed: {
    ...mapState(["listes", "searchResult"])
  },
  methods: {
    ...mapMutations(["searchData"]),
    //请求分类数据
    find(item) {
      this.axios({
        method: "GET",
        url: "api/byclass",
        params: {
          appkey: this.appkey,
          classid: item.classid,
          strt: 0,
          num: 50
        }
      })
        .then(res => {
          this.searchData(res);
          this.$router.push({ name: "About" });
          sessionStorage.setItem("about", JSON.stringify(res));
        })
        .catch(err => {
             
        });
    }
  }
};
</script>

<style lang="less" scoped>
div {
  max-height: 660px;
  overflow: auto;
  ul {
    max-height: 660px;
    overflow: visible;
    li {
      border: 1px solid #999;
      margin-top: 20px;
      margin-left: 15px;
      text-align: center;
      width: 75px;
      color: #999;
      padding: 5px 0;
      font-size: 12px;
      border-radius: 10px;
    }
  }
}
</style>